import React from 'react';
import {Container, Row, Col} from 'reactstrap';
import Certavue from '../utility/Certavue';

const CompanyRegistrationCompletedPage: React.FC = () => {
    return (
        <Container className="mt-5 mb-5">
            <Row className="justify-content-center">
                <Col xs={12} md={10} lg={8}>
                    <div className="text-center mb-4">
                        <Certavue />
                        <h2 className="mt-3">Company Registration Completed</h2>
                    </div>
                </Col>
            </Row>
        </Container>
    );
};

export default CompanyRegistrationCompletedPage;
